/* common */
/* body * {outline: 1px dotted red;} */
.clearMarColl::before,
.clearMarColl::after{ display: table; content: '';}
.Login button {cursor: pointer;}
.Login input[type=submit] {cursor: pointer;}

/* layout */

.Login .inner{ display: flex; flex-direction: column; align-items: center; gap: 100px; box-sizing: border-box; padding: 80px; background-color: #ffffff; border-radius: 10px; box-shadow: 0 5px 20px 3px rgba(0, 0, 0, .5); width: 480px; margin: 100px auto;}

.Login .inner form{display: flex; flex-direction: column; width: 100%; gap: 20px; font-size: 24px;}
.Login .inner form input::placeholder{ font-size: 16px;}
/* #wrap .inner form>*{display: block;} */

.Login .inner form button,
.Login .inner form input{padding: 8px; border-radius: 6px; border: 1px solid #ccc; font-size: 16px;}
.Login .inner form input:focus{outline: 1px solid #a8a8a8;}

.Login .inner form button,
.Login .inner form input[type=submit]{border-radius: unset; background-color: orange; text-align: center; font-size: 20px; color: white; border-radius: 3px; border: none; position: relative; overflow: hidden; transition: .4s; font-family: 'SOYOMapleBoldTTF', sans-serif;}
.Login .inner .login--form button::after{width: 100%; height: 100%; background-color: rgba(255, 255, 255, .2); left: -100%; top: 0; display: block; content: "";position: absolute; transition: .4s;}

.Login .inner .login--form button:hover::after{ left: 0;}
.Login .inner form input[type=submit]:hover{ border-radius: 18px;}



.Login h1{width: 150px;height: 150px;}
.Login h1 img{width: 100%; object-fit: contain;}



.Login #register--modal{position: fixed; width: 100vw; height: 100vh; background-color: rgba(0, 0, 0, .5); top: 0; display: none; left: 0;}

.Login #register--modal .inner{width: 640px; margin-top: 300px; position: relative;}
.Login #register--modal .inner button.close{display: block; position: absolute;right: 20px; top: 20px; background-color: rgb(255, 126, 126); color: #ffffff; border-radius: 50%; padding: 2px; transition: .5s;}
.Login #register--modal .inner button.close:hover{ background-color: red;}
.Login #register--modal .inner button.close span{ display: block;}



.Login #register--modal .inner .register--nickname--wrap{ display: flex; gap: 30px;}
.Login #register--modal .inner .register--nickname--wrap button{position: unset; transition: .3s;}
.Login #register--modal .inner .register--nickname--wrap button:hover{background-color: rgb(255, 209, 117);}
.Login #register--modal .inner .register--nickname--wrap input{ flex: 3;}
/* #register--modal .inner form p{ font-size: 1rem; color: chartreuse; display: none;} */

.Login .success{ display: none; color: #00ff00; font-size: 1rem;}
.Login .failed{ display: none; color: #ff0000; font-size: 1rem;}